<template>
  <div>
    <div style="width: 90%;margin: 30px auto">
      <el-row :gutter="20">
        <el-col>
          <h1>区块链电子存证平台</h1>
        </el-col>
        <el-col>
          <h4><em>欢迎用户‘{{ username }}’</em></h4>
        </el-col>
        <el-col align="right">
          <el-button type="danger" @click="logout">退出登录</el-button>
        </el-col>
      </el-row>
    </div>
    <div style="width: 90%;height: auto;margin: auto">
      <el-card>
        <el-descriptions :column="3">
          <el-descriptions-item>
            <el-upload style="margin: auto"
                       class="upload-demo"
                       ref="upload"
                       action=""
                       :on-preview="handlePreview"
                       :on-remove="handleRemove"
                       :http-request="submitUpload"
                       :on-change="handleChange"
                       :file-list="uploadFile"
                       :auto-upload="false"
                       :limit="1"
            >
              <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
              <div style="margin-left: 0" slot="tip" class="el-upload__tip">选取文件，点击上传到服务器完成上传（限单个文件）</div>
            </el-upload>
          </el-descriptions-item>
          <el-descriptions-item>
            <div style="margin: auto">
              <el-input
                  @keyup.enter.native="searchClick"
                  placeholder="输入文件Hash查询..."
                  prefix-icon="el-icon-search"
                  autosize
                  style="width: 200px"
                  v-model="keywords">
              </el-input>
              <el-button style="margin-left: 10px" size="small" type="primary" icon="el-icon-search" @click="searchClick(keywords)">查询文件</el-button>
            </div>
          </el-descriptions-item>
          <el-descriptions-item>
            <el-upload
                style="margin: auto;position: relative;top: 20%"
                class="verify-demo"
                ref="verify"
                action=""
                :http-request="submitVerify"
                :on-change="verifyChange"
                :file-list="newFile"
                :limit="1"
            >
              <el-button size="small" type="warning" @click="submitVerify">点击验证</el-button>
            </el-upload>
          </el-descriptions-item>
        </el-descriptions>
      </el-card>
    </div>
    <div style="margin-bottom: 30px;display: flex;justify-content: center;align-items: center">
      <el-dialog title="文件信息" :visible.sync="searchTableVisible">
        <el-descriptions border :column="1">
          <el-descriptions-item>
            <template slot="label">文件名</template>{{searchFile.length === 0 ? null : searchFile[0].filename}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件大小</template>{{searchFile.length === 0 ? null : searchFile[0].filesize}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件上传时间</template>{{searchFile.length === 0 ? null : searchFile[0].lastUpdateTime}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件Hash</template>{{searchFile.length === 0 ? null : searchFile[0].filehash}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件状态</template>
            <template>
              <el-tag :type="searchFile.length !== 0 && searchFile[0].status === '未上链' ? 'danger' : 'success'"
                      disable-transitions>{{searchFile.length === 0 ? null : searchFile[0].status}}</el-tag>
            </template>
          </el-descriptions-item>
        </el-descriptions>
        <template>
          <el-button size="small" type="success" :loading="loadingTxt" @click="deployClick(searchFile[0])">上链</el-button>
          <el-button size="small" type="danger" @click="searchTableVisible=false;keywords=''">返回</el-button>
        </template>
      </el-dialog>
    </div>
    <div style="width: 90%;margin: 10px auto">
      <el-card>
        <el-table
            :data="fileData"
            stripe
            :header-cell-style="headStyle"
            :cell-style="headStyle"
        >
          <el-table-column label="序号" type="index" :index="indexMethod"></el-table-column>
          <el-table-column prop="filename" label="文件名" ></el-table-column>
          <el-table-column prop="filehash" label="文件Hash"></el-table-column>
          <el-table-column prop="filesize" label="文件大小"></el-table-column>
          <el-table-column prop="lastUpdateTime" label="文件上传时间"></el-table-column>
          <el-table-column
              prop="status"
              label="文件状态"
              :filters="[{ text: '未上链', value: '未上链' }, { text: '已上链', value: '已上链' }]"
              :filter-method="filterStatus"
              filter-placement="bottom-end"
          >
            <template slot-scope="scope">
              <el-tag
                  :type="scope.row.status === '未上链' ? 'danger' : 'success'"
                  disable-transitions>{{scope.row.status}}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button size="mini" type="success" :loading="scope.row.loading" @click="deployClick(scope.row)">上链</el-button>
              <el-button size="mini" type="primary" @click="searchClick(scope.row.filehash)">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>
    <div class="verifyDialog" style="margin-bottom: 30px;display: flex;justify-content: center;align-items: center">
      <el-dialog title="验证结果" :visible.sync="verifyTableVisible">
        <el-descriptions title="验证文件" border :column="1">
          <el-descriptions-item>
            <template slot="label">文件名</template>{{newFile.length === 0 ? null : newFile[0].raw.name}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件Hash</template>{{oldFile.length === 0 ? null : oldFile.filehash}}
          </el-descriptions-item>
        </el-descriptions>
        <el-descriptions title="原始文件" border :column="1">
          <el-descriptions-item>
            <template slot="label">文件名</template>{{oldFile.length === 0 ? null : oldFile.filename}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件大小</template>{{oldFile.length === 0 ? null : oldFile.filesize}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件上传时间</template>{{oldFile.length === 0 ? null : oldFile.lastUpdateTime}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件Hash</template>{{oldFile.length === 0 ? null : oldFile.filehash}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">文件状态</template>
            <template>
              <el-tag :type="oldFile.length !== 0 && oldFile.status === '未上链' ? 'danger' : 'success'"
                      disable-transitions>{{oldFile.length === 0 ? null : oldFile.status}}</el-tag>
            </template>
          </el-descriptions-item>
        </el-descriptions>
        <template>
          <el-button size="small" type="danger" @click="verifyTableVisible=false;newFile=[];oldFile=[];">返回</el-button>
        </template>
      </el-dialog>
    </div>
    <div class="deployDialog" style="margin-bottom: 30px;display: flex;justify-content: center;align-items: center">
      <el-dialog title="存证信息" :visible.sync="deployTableVisible">
        <el-descriptions border :column="1" >
<!--          <el-descriptions-item>-->
<!--            <template slot="label">文件名</template>{{evidence.length === 0 ? null : searchFile[0].filename}}-->
<!--          </el-descriptions-item>-->
          <el-descriptions-item>
            <template slot="label">文件Hash</template>{{evidence.length === 0 ? null : evidence[0].filehash}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">合约地址</template>
            <el-link type="primary" :href="adrHref" v-bind:target="'_blank'">{{evidence.length === 0 ? null : evidence[0].contractAddress}}</el-link>
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">存证时间戳</template>{{evidence.length === 0 ? null : evidence[0].timeStamp}}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">存证交易Hash</template>
            <el-link type="primary" :href="txHref" v-bind:target="'_blank'">{{evidence.length === 0 ? null : evidence[0].transactionHash}}</el-link>
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">区块号</template>{{evidence.length === 0 ? null : evidence[0].blockNumber}}
          </el-descriptions-item>
        </el-descriptions>
        <template>
          <el-button size="small" type="danger" @click="deployTableVisible=false">返回</el-button>
        </template>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "Index",
  data() {
    return {
      uploadFile: [],
      formData: new FormData(),
      fileData: [],
      searchTableVisible: false,
      searchFile: [],
      keywords: '',
      newFile: [],
      oldFile: [],
      verifyTableVisible: false,
      deployTableVisible: false,
      evidence: [],
      adrHref: '',
      txHref: '',
      loadingTxt: false
    }
  },
  //自动获取后端数据
  mounted() {
    this.fetchData();
  },
  methods: {
    logout() {
      sessionStorage.clear()
      this.$router.replace("/login")
    },

    //文件上传
    handleChange(file, fileList) {
      this.uploadFile.push(file);
      console.log(this.uploadFile)
    },
    submitUpload(params) {
      // console.log(this.fileUpload)
      // this.fileUpload.forEach((item, index) => {
      //   formData.append("file", item.raw);
      // });
      // console.log(formData)
      this.formData.append('file', this.uploadFile[0].raw)
      this.request.post('/file/upload', this.formData, {
        headers: {
          "Content-Type": "multipart/form-data; charset=utf-8"
        }
      })
          .then(res => {
            if (res.code === 200) {
              //使用vue-router 路由到指定页面，该方式称之为编程式导航
              // this.$router.push("/login");
              this.$message.success(res.msg)
              this.fetchData()
              this.uploadFile = []
            } else {
              // console.log(res.data);
              this.formData.delete('file');
              this.$message.error(this.uploadFile[0].raw.name + "文件已存在")
              this.uploadFile = []
            }
          })
    },
    handleRemove(file, fileList) {
      console.log(file, fileList)
    },
    handlePreview(file) {
      console.log(file)
    },
    //编制表格索引
    indexMethod(index) {
      return index + 1;
    },
    //实现文件状态过滤
    filterStatus(value, row) {
      return row.status === value;
    },
    //获取文件列表
    fetchData() {
      this.request.get("/file/list").then(res => {
        if (res.code === 200) {
          // console.log(res)
          this.fileData = res.data;
          // 实现表格每一行的button loading
          this.fileData.map(item => {
            this.$set(item, 'loading', false)
            return item
          })
        }
      })
    },
    //表头居中
    headStyle() {
      return "text-align:center"
    },
    //实现文件查询
    searchClick(keywords) {
      // this.$emit('onSearch')
      this.request.get("/file/search?filehash=" + keywords).then(res => {
        if(res.code === 200) {
          console.log(res)
          this.searchTableVisible = true;
          this.searchFile.push(res.data);
        } else {
          this.$message.error(res.data)
          this.keywords = ''
        }
      })
    },
    //验证
    verifyChange(file, fileList) {
      this.newFile.push(file);
      console.log(this.newFile)
    },
    submitVerify() {
      this.formData.append('file', this.newFile[0].raw)
      this.request.post('/file/verify', this.formData, {
        headers: {
          "Content-Type": "multipart/form-data; charset=utf-8"
        }
      })
          .then(res => {
            // console.log(res)
            if (res.code === 200) {
              this.$message.success("验证成功");
              console.log(res.data)
              this.oldFile = res.data;
              this.verifyTableVisible = true;
              this.formData.delete('file');
            } else {
              // console.log(res.data);
              this.formData.delete('file');
              this.$message.error(this.newFile[0].raw.name + "文件不存在，验证失败")
              this.newFile = []
            }
          })
    },
    //上链
    deployClick(file) {
      file.loading = true
      this.loadingTxt = true
      this.request.post("/chain/deploy?filehash=" + file.filehash).then(res => {
        if(res.code === 200) {
          console.log(res)
          file.loading = false
          this.loadingTxt = false
          this.$message.success("文件‘" + file.filename + "'上链成功！")
          this.evidence.push(res.data)
          this.adrHref = "https://goerli.etherscan.io/address/" + this.evidence[0].contractAddress
          this.txHref = "https://goerli.etherscan.io/tx/" + this.evidence[0].transactionHash
          this.deployTableVisible = true
          this.fetchData()
        } else {
          this.$message.error(res.data)
        }
      })
    },
  },
  created() {
    this.username = sessionStorage.getItem("sysUser");
  }
}
</script>
<style scoped>

</style>
